.side_menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 7%;
  background-color: transparent;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.audio {
  width: 5%;
  height: 100%;
  transform: scale(0.6);
}

.elements {
  width: 95%;
  height: 100%;
  display: flex;
  justify-content: right;
}

.side_menu ul {
  width: 60%;
  height: 100%;
  list-style: none;
  display: flex;
  justify-content: right;
  align-items: center;
  box-shadow: 1px 4px 10px black;
  background-color: rgba(255, 255, 255, 0.2);
  border-bottom-left-radius: 30%;

}

.side_menu li {
  width: 90%;
  height: 90%;
  font-size: larger;
  margin-left: 2%;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 720px) {
  .side_menu li {
    font-size: small;
  }

  .audio {
    transform: scale(0.4);
  }
}

@media only screen and (max-width: 420px) {
  .side_menu li {
    font-size: xx-small;
  }

  .audio {
    transform: scale(0.0);
  }
}
